import React,{Component} from 'react';
import './Header.css';
import {Link} from 'react-router-dom';
import {Modal} from 'react-responsive-modal';
import 'react-responsive-modal/styles.css';




const menuUrl = "https://kinzoinstitute.onrender.com/Categories"


class MenuCategory extends Component {
    constructor(props) {
        super(props);
        console.log(">>> inside MenuCatState", props)

        this.state={
            menuCategory:'',
            adverts: false
            
        }
    }

    onCloseModalAdverts(){
        this.setState({
            adverts: false
        })
    }

    onOpenModalAdverts(){
        
        this.setState({
            adverts: true
        })
        
        
    }

    renderMenu = (data) => {
        
        if (data){
            data.sort((a, b) => a._id - b._id);
            return data.map((item) => {
                return(
                    <>
                       
                        <div className="card addColor">
                            <div className='courseImage'>
                                <img src={item.Productimage} className="card-img-top" alt="menuImages"/>
                            </div>
                            <div className='coursedescription'>
                                <center className = "container">
                                    <p className="card-title setHedd">{item.heading}</p>
                                </center>
                            </div>
                            
                            <div className="card-body mobileViewRel">
                                <center>
                                    <h4 className="card-title mobileViewAbs">{item.product}</h4>
                                </center>
                                
                            </div>
                            <div className="overlay"><br/>
                                <h5 className="note">{item.product}</h5>
                                <hr style={{color:"gold"}}/>
                                <center>
                                    <br/><br/>
                                    <p>
                                        <Link to={`/MenuItems/${item._id}`} key={item._id} style={{fontFamily:"Georgia, 'Times New Roman', Times, serif", fontSize:"20px", textDecoration:"none", color:"white", fontWeight:"bold"}}><button type="button" className="btn btn-outline-warning" onClick={localStorage.setItem('catID', item._id)}>Explore Course</button></Link>
                                    </p>
                                </center>
                            </div>
                        </div>
                       
                    </>
                            
                )   

            })
        }
        else{
            return(
                <center>
                    <div>
                        <div class="spinner-border text-primary" role="status">
                            <span class="visually-hidden">Loading...</span>
                        </div>
                        <div class="spinner-border text-secondary" role="status">
                            <span class="visually-hidden">Loading...</span>
                        </div>
                        <div class="spinner-border text-success" role="status">
                            <span class="visually-hidden">Loading...</span>
                        </div>
                        <div class="spinner-border text-danger" role="status">
                            <span class="visually-hidden">Loading...</span>
                        </div>
                        <div class="spinner-border text-warning" role="status">
                            <span class="visually-hidden">Loading...</span>
                        </div>
                    </div>
                </center>

                
            )
        }   
    }


    render(){
        console.log(">>> Inside menuCatRender", this.state)
        
        return(
            <>
                
                <div className="container putinplace">
                    <div className='fitIn'>
                        <center>
                            <h5 className="text">Program Courses</h5>
                        
                        
                            <div className="row">
                                <div className ="col">
                                    {this.renderMenu(this.state.menuCategory)}
                                </div>

                                
                            
                            </div>
                        </center>
                    
                    </div>

                </div>

                <Modal open={this.state.adverts} onClose={()=>this.onCloseModalAdverts()} center>

                    <div className="advertbgn">
                        
                        <div>
                            <img className="imgsizeCar2" src="https://i.imgur.com/2gD9MbF.png" alt="FirstSlide"/>
                            
                        </div><br/>

                        <center>
                            <h4 className='editAdHeading'> Join the next cohort. Upskill with the best Splunk Academy</h4><br/>
                            <p className='editAdHeading2'> Unleash your fullest potentials and become a data virtuoso. Splunk is the world's leading data analysis and monitoring platform.</p>
                            
                        </center>
                        <br/>
                        <center><button className="btn btn-warning editAdbutton" onClick={()=>this.onCloseModalAdverts()}>Register Now</button></center><br/>

                                                 
                       
                    </div>
                    
                </Modal>
                
            
            </>
            
        )
    }

    componentDidMount(){
        console.log(">>> Inside MenuCategoryDidMount", this.state)
        fetch(menuUrl, {method:'GET'})
        .then((res) => res.json())
        .then((data) => {
            this.setState({menuCategory:data})
        })

        setTimeout(() => {
            this.setState({ adverts: true });
        }, 8000);


        
        


    }
}

export default MenuCategory;